@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: "Roboto",sans-serif;
    color: #dddddd;
    font-weight: 300;
    scrollbar-track-color: transparent;
}
:root{
    --yellow:#eec549;
    --red:#cc3330;
    --blue:#65a7dd;
    --green:#95c278;
    --gray:#3E3E3C;
    --dark-gray:#212121;
}
/* width */ 
::-webkit-scrollbar {
    width: 10px;
    height:10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #212121;
    background: var(--dark-gray);
  }
  

.editor-page{
    background-color: #333333;
    width: 100vw;
    height:100vh;
}
.back-btn{
    height: 20px;
}
.back-btn img{
    height: 100%;
    -webkit-filter: invert(1);
            filter: invert(1);
    position: relative;
    top:3px;
    right:92%
}
.editor-page-header{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    width: 95%;
    margin:auto;
    padding-top: 20px;
    height: 18vh;
    text-align: right;
}

/* Editor section */

.editor-container{
    position: relative;
    width: 95%;
    margin: auto;
    height: 82vh;
}
.controls-wrapper{
    position: absolute;
    top: 0;
    width: 40%;
    height:15%;
    padding-bottom: 1rem;

}
.text-editor-wrapper{
    position: absolute;
    top: 15%;
    width: 40%;
    height: 85%;
    padding-bottom: 1rem;
}
.preview-frame-wrapper{
    position: absolute;
    left: 40%;
    width: 60%;
    height:100%;
    padding-left: 1rem;
    padding-bottom: 1rem;
}
.preview-frame{
    background-color: #212121;
    border: none;
    height: 100%;
    width:100%;
}

.logo{
    height: inherit;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logo-img{
    position: absolute;
    height:120%;
    top:-7vh;
}
.logo-text{
    position: absolute;
    top:7.5vh;
    width: 25vw;
    text-align: center;
    font-size:3.3vh;
}
.control-container{
    background-color: #21212150;
    height: 100%;
    display: flex;
    padding:0 10px
}
.run-btn,.stop-btn,.save-btn,.html-btn,.css-btn,.refresh-btn{
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-self: center;
    cursor: pointer;
    margin-top: 5px;
    padding: 0 12px;
}
.btn-img{
    height: 23px;
    vertical-align: middle;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 18px;
    display: inline;
}
.run-btn .btn-img{
    -webkit-filter: invert(79%) sepia(77%) saturate(736%) hue-rotate(338deg) brightness(100%) contrast(100%);
            filter: invert(79%) sepia(77%) saturate(736%) hue-rotate(338deg) brightness(100%) contrast(100%);
}
.run-btn{
    color: var(--yellow);
}
.stop-btn .btn-img{
    -webkit-filter: invert(21%) sepia(69%) saturate(4239%) hue-rotate(352deg) brightness(88%) contrast(104%);
            filter: invert(21%) sepia(69%) saturate(4239%) hue-rotate(352deg) brightness(88%) contrast(104%);
}
.stop-btn{
    color: var(--red);
}
.save-btn .btn-img{
    -webkit-filter: invert(51%) sepia(91%) saturate(1197%) hue-rotate(184deg) brightness(103%) contrast(80%);
            filter: invert(51%) sepia(91%) saturate(1197%) hue-rotate(184deg) brightness(103%) contrast(80%);
}
.save-btn{
    color: var(--blue);
}
.refresh-btn .btn-img{
    -webkit-filter: invert(87%) sepia(5%) saturate(3617%) hue-rotate(43deg) brightness(90%) contrast(74%);
            filter: invert(87%) sepia(5%) saturate(3617%) hue-rotate(43deg) brightness(90%) contrast(74%);
}
.refresh-btn{
    color: var(--green);
}
.refresh-btn-deactive,.refresh-btn-deactive .btn-img{
    -webkit-filter: invert(0.2);
            filter: invert(0.2);
    color: #666666;
}
.save-btn:active{
    -webkit-filter: invert(1);
            filter: invert(1);
}

.html-css-btn{
    align-self: center;
    flex-grow: 1;
    text-align: right !important;
    font-size: 15px;
    text-align: right;
    display: flex;
    flex-direction: column;
}
.html-btn,.css-btn{
    text-align: center;
    width: 120px;
    align-self: flex-end;
    padding: 2px;
    vertical-align: center;
}
.html-btn .btn-img,.css-btn .btn-img{
    position: relative;
}
.html-btn .btn-img,.html-btn{
    color:#FF5733 ;
}
.css-btn .btn-img,.css-btn{
    color: var(--blue);
}
.html-btn-active{
    background-color: #FF5733;
    color: white !important;
}
.css-btn-active{
    background-color: var(--blue);
    color: white !important;
}
.text-editor-container{
    height:100%;
}
.codemirror-wrapper{
    height: 64vh ;
    background-color: #212121;
    padding: 5px;
}
.CodeMirror{
    height:60vh !important;
    background-color: var(--dark-gray) !important;
    color:#dddddd !important;
}
.cm-s-material-darker .CodeMirror-gutters {
    background: #212121;
    color: #666666 !important;
  }
  
  .cm-s-material-darker .CodeMirror-guttermarker,
  .cm-s-material-darker .CodeMirror-guttermarker-subtle,
  .cm-s-material-darker .CodeMirror-linenumber {
    color: #666666 !important;
  }
  
  .cm-s-material-darker .CodeMirror-cursor {
    border-left: 2px solid var(--yellow) !important;
  }
  
  .cm-s-material-darker div.CodeMirror-selected {
    background: rgba(97,97,97,0.3) !important;
  }
  
  .cm-s-material-darker.CodeMirror-focused div.CodeMirror-selected {
    background: rgba(97,97,97,0.3)!important;
  }
  
  .cm-s-material-darker .cm-keyword {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-operator {
    color:var(--red) !important;
  }
  
  .cm-s-material-darker .cm-variable-2 {
    color: #cccccc !important;
  }
  
  .cm-s-material-darker .cm-variable-3,
  .cm-s-material-darker .cm-type {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-builtin {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-atom {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-number {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-def {
    color: var(--blue) !important;
  }
  
  .cm-s-material-darker .cm-string {
    color: var(--green) !important;
  }
  
  .cm-s-material-darker .cm-string-2 {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-comment {
    color: #666666 !important;
  }
  
  .cm-s-material-darker .cm-variable {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-tag {
    color: var(--red) !important;
  }
  
  .cm-s-material-darker .cm-meta {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-attribute {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-property {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-qualifier {
    color: var(--yellow) !important;
  }
  
  .cm-s-material-darker .cm-variable-3,
  .cm-s-material-darker .cm-type {
    color: var(--yellow) !important;
  }
  
  
  .cm-s-material-darker .cm-error {
    color: #dddddd !important;
    background-color: var(--red) !important;
  }
  
  .cm-s-material-darker .CodeMirror-matchingbracket {
    text-decoration: underline;
    color: white !important;
  }
.editor-tabs{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction:column;
}
.current-text-editor-wrapper{
    flex-grow: 1;
    overflow-y: auto;
    box-sizing: border-box;
}
.editor-tabs-list{
    display: flex;
    flex-direction: row;
    overflow-x: auto;
}
.editor-tab,.new-tab-btn{
    max-width: 130px;
    min-width: 90px;
    flex-grow: 1;
    background-color: #21212120;
    text-align: center;
    border-right:1px solid #dddddd50;
    padding: 3px;
    cursor: pointer;
}
.editor-tab-name{
    max-width: 80%;
    overflow-x:hidden;
}
.editor-tab-name:hover{
    color: gold;
}
.new-tab-btn{
    flex-grow: 0.01;
    min-width: 30px;
    border: none;
}
.new-tab-btn:hover, .editor-tab:hover{
    background-color: #21212180;
    color: rgb(99, 200, 240);
    font-weight: 400;
}
.new-tab-btn:active{
    background-color: #212121;
}
.remove-tab-btn{
    display: inline;
    float: right;
    padding-right: 3px;
}
.remove-tab-btn:hover{
    color: orangered;
    font-weight: 400;
}
.active-tab{
    background-color: #212121;
}

